<template>
  <b-sidebar
    id="sidebar"
    sidebar-class="sidebar-lg"
    :visible="isSidebarActive"
    bg-variant="white"
    shadow
    backdrop
    no-header
    right
    @hidden="()=> {
      resetForm(),
      resetData()
    }"
    @change="(val) => isSidebarActive = val"
  >
    <template #default="{ hide }">
      <div
        class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1"
      >
        <h5 class="mb-0">
          {{ team.id ? "Editar" : "Registrar" }} Grupo
        </h5>

        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click="hide"
        />
      </div>

      <!-- BODY -->
      <validation-observer
        #default="{ handleSubmit }"
        ref="refFormObserver"
      >
        <b-form
          ref="form"
          class="p-2"
          @submit.prevent="handleSubmit(onSubmit)"
        >
          <b-alert
            v-if="states.name.isInvalid"
            show
            variant="danger"
          >
            <div class="alert-body">
              <feather-icon
                class="mr-25"
                icon="AlertCircleIcon"
              />
              <span class="ml-25">{{ states.name.message }}</span>
            </div>
          </b-alert>
          <validation-provider
            #default="validationContext"
            name="name"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Nombre"
              label-for="name"
              :invalid-feedback="states.name.mensaje"
            >
              <b-form-input
                id="name"
                v-model="team.name"
                placeholder="Nombre"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <b-form-group
            label="Descripcin"
            label-for="descripcion"
          >
            <b-form-textarea
              id="descripcion"
              v-model="team.description"
              placeholder="Descripcion opcional"
              rows="3"
            />
          </b-form-group>
          <validation-provider
            #default="validationContext"
            name="units"
            rules="required"
          >
            <b-form-group
              label="Unidades"
              label-for="units"
            >
              <v-select
                v-model="team.units"
                multiple
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="unitsList"
                label="plate"
                :clearable="true"
                input-id="units"
              >
                <template v-slot:no-options="{ search, searching }">
                  <template v-if="searching">
                    No se encontraron resultados para <em>{{ search }}</em>.
                  </template>
                  <em
                    v-else
                    style="opacity: 0.5"
                  >
                    Comienza a escribir para buscar.
                  </em>
                </template>
              </v-select>
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <b-form-group
            label="Activo"
            label-for="status"
          >
            <b-form-checkbox
              v-model="team.status"
              class="custom-control-success"
              name="status"
              switch
            />
          </b-form-group>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-2 mt-4"
            type="submit"
            block
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSaving"
              small
            />
            <span class="align-middle ml-1">
              {{ team.id ? "Actualizar" : "Registrar " }}
            </span>
          </b-button>
        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
import {
  BAlert,
  BSidebar,
  BForm,
  BFormCheckbox,
  BFormGroup,
  BFormTextarea,
  BFormInput,
  BFormInvalidFeedback,
  BButton,
  BSpinner,
} from 'bootstrap-vue'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import vSelect from 'vue-select'
import useCatalogs from '@/composables/useCatalogs'
import useTeam from '../composables/useTeam'

localize({
  en: {
    messages: {
      // generic rule messages...
    },
    fields: {
      name: {
        required: 'Introduce un nombre para el grupo',
      },
      units: {
        required: 'Selecciona un almenos una unidad',
      },
    },
  },
})

export default {
  components: {
    BAlert,
    BSpinner,
    BSidebar,
    BForm,
    BFormCheckbox,
    BFormTextarea,
    BFormGroup,
    BFormInput,
    BFormInvalidFeedback,
    BButton,
    vSelect,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  setup(__, { emit }) {
    const resetuserData = () => emit('reset-data')

    const { refFormObserver, getValidationState, resetForm } = formValidation(resetuserData)

    const {
      unitsList,
      getUnitsWithoutTeam,
      // getUnitsWithoutTeamExcept,
    } = useCatalogs()

    const {
      team,
      isSaving,
      isSidebarActive,
      save,
      edit,
      resetData,
      states,
    } = useTeam()

    const onSubmit = () => {
      save().then(() => {
        emit('reload')
      })
    }

    const open = async () => {
      await getUnitsWithoutTeam()
      isSidebarActive.value = true
    }

    const getElement = async id => {
      await getUnitsWithoutTeam()
      await edit(id)
    }

    return {
      team,
      isSaving,
      isSidebarActive,
      states,

      unitsList,

      onSubmit,
      refFormObserver,
      getValidationState,
      resetForm,
      open,
      getElement,
      resetData,

      required,
    }
  },
}
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";

#add-new-user-sidebar {
  .vs__dropdown-menu {
    max-height: 200px !important;
  }
}
</style>
