<template>
  <div>
    <TeamsTable
      ref="teamsTable"
      @handle-edit="(id) => $refs.sidebarTeam.getElement(id)"
    >
      <template v-slot:plus>
        <b-button
          variant="primary"
          @click="$refs.sidebarTeam.open()"
        >
          <span class="text-nowrap">Registrar Nuevo</span>
        </b-button>
      </template>
    </TeamsTable>
    <SidebarTeam
      ref="sidebarTeam"
      @reload="$refs.teamsTable.fetchList()"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { BButton } from 'bootstrap-vue'
import TeamsTable from './components/TeamsTable.vue'
import SidebarTeam from './components/SidebarTeam.vue'

export default {
  components: {
    BButton,
    TeamsTable,
    SidebarTeam,
  },
  setup() {
    const sidebarTeam = ref()
    const teamsTable = ref()
    return {
      teamsTable,
      sidebarTeam,
    }
  },
}
</script>

